import "./rate.scss";

$(function () {
  $(".rating__checkbox").on("click", function () {
    var elem = $(this).parent().parent().find(".rating__checkbox");
    //–ü–æ–∏—Å–∫ —ç–ª–µ–º–µ–Ω—Ç–∞ —Å –∞—Ç—Ä–∏–±—É—Ç–æ–º checked –∏ –µ–≥–æ —É–¥–∞–ª–µ–Ω–∏–µ
    for (var i = 0; i < elem.length; i++) {
      if ($(elem[i]).attr("checked")) {
        $(elem[i]).attr("checked", false);
      }
    }

    //–£—Å—Ç–∞–Ω–æ–≤–∫–∞ –∞—Ç—Ä–∏–±—É—Ç–∞ checked –Ω–∞–∂–∞—Ç–æ–º—É —ç–ª–µ–º–µ–Ω—Ç—É
    $(this).attr("checked", true);

    var elem = $(this).parent().parent().find(".rating__checkbox");
    var star = $(this).parent().parent().find(".rating__star");

    //–ü–æ–∏—Å–∫ —ç–ª–µ–º–µ–Ω—Ç–∞ —Å –∞—Ç—Ä–∏–±—É—Ç–æ–º checked
    var trigger = 0;
    console.log("trigger " + trigger);
    console.log("--------------------------------------------");
    for (var j = elem.length - 1; j >= 0; j--) {
      console.log("j " + j);

      if ($(elem[j]).attr("checked")) {
        trigger = 1;
      }

      if (trigger == 1) {
        //–ù–∞ –Ω–∞–∂–∞—Ç–æ–º –∏ –≤—Å–µ—Ö –ø—Ä–µ–¥—à–µ—Å—Ç–≤—É—é—â–∏—Ö –µ–º—É —ç–ª–µ–º–µ–Ω—Ç–∞—Ö –∑–∞–≥–æ—Ä–∞–µ—Ç—Å—è –∑–≤–µ–∑–¥–∞
        //$(this).parent().children(".rating__star").text("star");

        $(star[j]).text("star");
        console.log("–≥–æ—Ä–∏—Ç");
      } else {
        console.log("trigger " + trigger);
        //–≤—ã–∫–ª—é—á–∞–µ–º –∑–≤–µ–∑–¥—É
        $(star[j]).text("star__border");
        console.log("trigger " + trigger);
        console.log("–Ω–µ–≥–æ—Ä–∏—Ç");
      }
      console.log("j " + j);
    }

    //console.log();
  });
});
